.image8Icon {
  position: relative;
  width: 6.31rem;
  height: 6.31rem;
  flex-shrink: 0;
  object-fit: cover;
}
.items,
.potato {
  position: relative;
  font-weight: 600;
}
.items {
  font-size: var(--font-size-sm);
  font-weight: 500;
  color: var(--grey);
}
.image8Parent,
.potatoParent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-8xs);
}
.image8Parent {
  border-radius: var(--br-10xs);
  background-color: var(--color-ivory);
  height: 12.13rem;
  flex-shrink: 0;
  padding: var(--padding-lgi) var(--padding-29xl);
  box-sizing: border-box;
  justify-content: flex-end;
  gap: var(--gap-3xs);
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--black);
  font-family: var(--font-quicksand);
}
